nav a:hover {
  color: #FFBD3A;
  font-weight: bold;
}
nav a:hover path{
  fill: #FFBD3A;
}
nav a:active path {
  fill: #01F0D1;
}
nav a:active {
  font-weight: bold;
  color: #01F0D1;
}

nav ul  {
